import permision from "@/common/permission.js"
import uniPopup from "../../components/uni-popup/uni-popup.vue";
var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
 
export default{
	components:{
		uniPopup
	},
	data(){
		return{   
			showpopup:true,
			  title: 'picker',
			index: 1,
			
			    arryObject: [{
			                              id: 1,
			                              name: '行业新闻' ,
										  
			                          },
			                          {
			                             id: 2,
			                              name: '行业消息',
			                             
			                          },
			                          {
			                             id: 3,
			                              name: '人事需求'
			                          },
			                          {
			                            id: 4,
			                              name: '食材需求'
			                          },
			                          {
			                             id: 5,
			                              name: '餐饮器件需求'
			                          },
			                       
			                      ],
			 
		
			
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
			
			
			modalName: null,
			textareaAValue: '',
			textareaBValue: '',
			form:{
				name:"",
				miaoshu:"",
				xuanze:"",
				content:""
			},
		
		}
	},
	onLoad() {
		/* this.quanxian(); */
	},
	methods:{
		hidePopup(){
			this.showpopup=false;
			
		},
		
		  bindPickerChange: function(e) {
			  this.index = e.target.value
			  this.form.xuanze=this.arryObject[e.detail.value].id;
		},
		tijioa(){
			let that =this;
			
			console.log("2")
		if(that.form.name == "" ||that.form.content == "" || that.form.miaoshu == "" || that.form.xuanze == "" || that.imgList==[]){
			console.log("1")
			uni.showToast({
			    title: '请填写必要信息',
			    duration: 2000,
				icon:"none"
			});
		}	
		else
		{
			 //const token = uni.getStorageSync('token');
			 uni.uploadFile({
			     url : this.url+'Home/content',
			     filePath: this.imageList[0],
				  
				 fileType:'image',
			     name: 'file',
				   header:{
					   
					   "Content-Type": "multipart/form-data",
				  // "token":token
				   },

			     formData: {
			     News_Title:that.form.name,
			 	News_Content:that.form.content,
			     News_Describe:that.form.miaoshu,
			     News_Type_ID:that.form.xuanze
			     },
			     success: function (uploadFileRes) {
			    
				  that.form.name="",
				  that.form.content="",
				  that.form.miaoshu="",
				  
				 that.imageList=[]
				 }
			    });
	}
	},	
	
	//删除图片
	delimage(index){
		uni.showModal({
			title:"提示",
			content:"确定要删除这张图片吗",
			success:(res)=>{
				if(res.confirm){
					this.imageList.splice(index,1)
				}
			}
		})
		},
		//上传图片
	chooseImage: async function() {









	
		if (this.imageList.length === 1) {return;}
		uni.chooseImage({
			sourceType: sourceType[this.sourceTypeIndex],
			sizeType: sizeType[this.sizeTypeIndex],
			count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
			success: (res) => {
				this.imageList = this.imageList.concat(res.tempFilePaths);
			},
			fail: (err) => {






				uni.getSetting({
					success: (res) => {
						let authStatus = false;
						switch (this.sourceTypeIndex) {
							case 0:
								authStatus = res.authSetting['scope.camera'];
								break;
							case 1:
								authStatus = res.authSetting['scope.album'];
								break;
							case 2:
								authStatus = res.authSetting['scope.album'] && res.authSetting['scope.camera'];
								break;
							default:
								break;
						}
						if (!authStatus) {
							uni.showModal({
								title: '授权失败',
								content: 'Hello uni-app需要从您的相机或相册获取图片，请在设置界面打开相关权限',
								success: (res) => {
									if (res.confirm) {
										uni.openSetting()
									}
								}
							})
						}
					}
				})

			}
		})
	},
	
	previewImage: function(e) {
		var current = e.target.dataset.src
		uni.previewImage({
			current: current,
			urls: this.imageList
		})
	},
	}
	
}
